/*@tailwind base;
@tailwind components;
@tailwind utilities;
*/
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.layout {
  min-height: 100vh;
}

header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
  flex-direction: row;
}
header > * {
  cursor: pointer;
}
.accessibility > * {
  margin: 0 0.1rem;
}
.accessibility .text-xl {
  font-size: 1.4em;
}
.accessibility .text-3xl {
  font-size: 1.8em;
}

main {
  margin: 0 auto;
  padding: 0 2rem;
  max-width: 700px;
}

main p {
  text-align: center;
  font-size: 1.1em;
}

main p:first-of-type {
  margin-top: 10vh;
}

textarea {
  width: 100%;
  border-radius: 0.5rem;
  border: 1px solid #ccc;
  margin: 0.3em 0;
  font-size: 1em;
  padding: 0.5em;
  box-sizing: border-box;
}

.actionContainer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 0 auto;
  width: 100%;
}

.actionContainer > * {
  display: flex;
  justify-content: center;
  padding: 4px;
  margin: 0.3em 0;
  border-radius: 10px;
  border: 3px solid #000;
  font-size: 1em;
  color: #000;
  cursor: pointer;
}

.size-10 {
  font-size: 1em;
}
.size-15 {
  font-size: 1.4em;
}
.size-20 {
  font-size: 2.2em;
}

.ver-space-between {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  height: calc(100vh - 6rem);
}

.circle-pink {
  border-radius: 50%;
  width: 27rem;
  height: 27rem;
  background-color: #f7b7ca;
  position: fixed;
  top: 26%;
  left: 17%;
  transform: translate(-50%, -50%);
  z-index: -1;
}
.circle-yellow {
  border-radius: 50%;
  width: 20rem;
  height: 20rem;
  background-color: #fddfac;
  position: fixed;
  top: 66%;
  left: 60%;
  transform: translate(-50%, -50%);
  z-index: -1;
}
.circle-blue {
  border-radius: 50%;
  width: 13rem;
  height: 13rem;
  background-color: #99d8f3;
  position: fixed;
  top: 84%;
  left: 84%;
  transform: translate(-50%, -50%);
  z-index: -1;
}