body{
    background-color: #EAE7E2;
}
.emotion-buttons {
    display: flex;
    flex-direction: row;
    justify-content: center;
    margin: 0 auto;
    width: 100%;
}

.emotion-buttons > button {
    padding: 0.5rem;
    margin: 1rem;
    border-radius: 10rem;
    background-color: white;
    border: 0;
    line-height: 0;
    cursor: pointer;
}

@media (max-width: 600px) {
    .emotion-buttons > button { 
        margin: 0.2rem;
    }
}

.emotion-buttons img {
    width: 100%;
    height: 100%;
}

.active > img {
    filter: brightness(0) invert(1);
}

.emotion-buttons > button.active.miserable {
    background-color: #7f58bc;
}

.emotion-buttons > button.active.sad {
    background-color: #5e69c3;
}

.emotion-buttons > button.active.neutral {
    background-color: #00c0cc;
}

.emotion-buttons > button.active.happy {
    background-color: #9acd65;
}

.emotion-buttons > button.active.awesome {
    background-color: #49b24b;
}

.layout {
    background-position-x: -45vw;
background-repeat: no-repeat;
background-size: 182vw;
background-position-y: 40%;
}